import React, { useEffect, useState } from 'react';
import styled from 'styled-components';

import { getScrollPosition } from '@/util';
import { Icon } from '@/components/common/Icon';

const TopButton = styled.button`
  position: fixed;
  right: 65px;
  bottom: 65px;
  width: 50px;
  height: 50px;
  text-decoration: none;
  cursor: pointer;

  @media (max-width: 720px) {
    display: none;
  }
`;

const TopButtonIcon = styled(Icon)`
  width: 50px;
  height: 50px;
`;

const FloatingTopBtn: React.FC = () => {
  const [display, setDisplay] = useState<'none' | 'block'>('none');
  const onScroll = () => setDisplay(getScrollPosition().y > 400 ? 'block' : 'none');
  const clickTopBtn = () => {
    window.scrollTo(0, 0);
    setDisplay('none');
  };

  useEffect(() => {
    if (window.innerWidth > 784) {
      window.addEventListener('scroll', onScroll);
    }

    return () => window.removeEventListener('scroll', onScroll);
  });

  return (
    <TopButton onClick={clickTopBtn} style={{ display }}>
      <TopButtonIcon iconType="TOP_BUTTON_ICON" />
    </TopButton>
  );
};

export default FloatingTopBtn;
